  color: rgba(0, 0, 0, 0.45);
}
.login-btn {
  width: 100%;
}
.ant-form-explain-holder {
  height: 20px;
}
input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px white inset !important;
}
.user-msg {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 12px;
  color: #ccc;
  margin-top: 16px;
  margin-bottom: 0;
}
.ant-form-explain-holder {
  line-height: 1.2;
}

.Main {
  min-height: 100vh;
}
.logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
  z-index: 5;
  padding: 0px;
  background-color: #fff;
  box-shadow: 0 1px 9px -1px rgba(0, 0, 0, 0.2);
}
.Header-right {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.Header-left {
  display: -webkit-flex;
  display: flex;
}
.Welcome {
  padding: 0 8px;
  white-space: nowrap;
}
.inform-bell {
  width: 40px;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 20px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.inform-bell:hover {
  background-color: #f0f2f5;
}
.inform-bell:hover .inform-bell-icon {
  font-size: 24px;
  color: #1da57a;
}
.inform-bell-icon {
  font-size: 24px;
  color: #bbbbbb;
}
.user {
  padding: 0 16px;
  margin-right: 32px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.user:hover {
  background-color: #f0f2f5;
}
.trigger {
  float: left;
  width: 64px;
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.trigger:hover {
  color: #1da57a;
  background-color: #f0f2f5;
}
.Header-Breadcrumb {
  display: inline-block;
  padding: 0 24px;
}
@media screen and (max-width: 1080px) {
  .trigger {
    float: left;
    width: 32px;
    font-size: 18px;
    line-height: 64px;
    padding: 0 8px;
    cursor: pointer;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
  }
  .user {
    padding: 0 16px;
    margin-right: 16px;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
  }
  .Welcome {
    color: #fff;
    width: 0%;
    padding: 0%;
  }
}
.Breadcrumb-div {
  display: inline-block;
  height: 64px;
}
.last-breadcrumb-item {
  color: rgba(0, 0, 0, 0.45);
}

.Footer {
  text-align: center;
  background-color: #fff;
  white-space: nowrap;
}
.ant-layout-footer {
  padding: 13px 50px;
}

.Content {
  /*定义内容区域内容超出时滚动条的样式 BEGIN*/
  /*定义内容区域内容超出时滚动条的样式 END*/
}
.Content ::-webkit-scrollbar {
  /* 血槽宽度 */
  width: 8px;
  height: 8px;
}
.Content ::-webkit-scrollbar-thumb {
  /* 拖动条 */
  background-color: #e6e6e6;
  border-radius: 6px;
}
.Content ::-webkit-scrollbar-thumb:hover {
  /* 拖动条伪类样式 */
  background-color: #e6e6e6;
  border-radius: 6px;
}
.Content ::-webkit-scrollbar-track {
  /* 背景槽 */
  border-radius: 6px;
}
.Content .Content-details {
  padding: 24px 24px 0 24px;
  background: #f0f2f5;
  height: calc(100vh - 136px);
  overflow: auto;
}
@media screen and (max-width: 1080px) {
  .Content {
    /*定义内容区域内容超出时滚动条的样式 BEGIN*/
    /*定义内容区域内容超出时滚动条的样式 END*/
  }
  .Content ::-webkit-scrollbar {
    /* 血槽宽度 */
    width: 2px;
    height: 2px;
  }
  .Content ::-webkit-scrollbar-thumb {
    /* 拖动条 */
    background-color: #8f8f8f;
    border-radius: 1px;
  }
  .Content ::-webkit-scrollbar-thumb:hover {
    /* 拖动条伪类样式 */
    background-color: #8f8f8f;
    border-radius: 1px;
  }
  .Content ::-webkit-scrollbar-track {
    /* 背景槽 */
    border-radius: 1px;
  }
  .Content .Content-details {
    padding: 12px 12px 0 12px;
    background: #f0f2f5;
    height: calc(100vh - 120px);
    overflow: auto;
  }
}

.Sider {
  background-color: #fff;
}
.ant-layout-sider-trigger {
