.Header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
  z-index: 5;
  padding: 0px;
  background-color: #fff;
  box-shadow: 0 1px 9px -1px rgba(0, 0, 0, 0.2);
}
.Header-right {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.Header-left {
  display: -webkit-flex;
  display: flex;
}
.Welcome {
  padding: 0 8px;
  white-space: nowrap;
}
.inform-bell {
  width: 40px;
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 20px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.inform-bell:hover {
  background-color: #f0f2f5;
}
.inform-bell:hover .inform-bell-icon {
  font-size: 24px;
  color: #1da57a;
}
.inform-bell-icon {
  font-size: 24px;
  color: #bbbbbb;
}
.user {
  padding: 0 16px;
  margin-right: 32px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.user:hover {
  background-color: #f0f2f5;
}
.trigger {
  float: left;
  width: 64px;
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.trigger:hover {
  color: #1da57a;
  background-color: #f0f2f5;
}
.Header-Breadcrumb {
  display: inline-block;
  padding: 0 24px;
}
@media screen and (max-width: 1080px) {
  .trigger {
    float: left;
    width: 32px;
    font-size: 18px;
    line-height: 64px;
    padding: 0 8px;
    cursor: pointer;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
  }
  .user {
    padding: 0 16px;
    margin-right: 16px;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
  }
  .Welcome {
    color: #fff;
    width: 0%;
    padding: 0%;
  }
}
.Breadcrumb-div {
  display: inline-block;
  height: 64px;
}
.last-breadcrumb-item {
  color: rgba(0, 0, 0, 0.45);
}
